import { Component, OnInit } from '@angular/core';
import { Http, Response } from '@angular/http';
import { Observable } from 'rxjs';
import 'rxjs/add/operator/toPromise';
import { FormFieldsVehicleInformationComponent } from '../form-fields-vehicle-information/form-fields-vehicle-information.component'
import { Vehicle } from '../../interfaces/vehicle';
@Component({
  selector: 'app-create-registration',
  templateUrl: './create-registration.component.html',
  styleUrls: ['./create-registration.component.scss']
})
export class CreateRegistrationComponent implements OnInit {

  vehicle: Vehicle = {
    v5cID:'',
    identity: 'DVLA',
    VIN: '13245',
    License: '',
    make: '',
    model: '',
    colour: '',
    Reg: '',
    year: null,
    FuelType: '',
    BodyType: '',
    StateLicenseNumber: '',
    VehicleType: '',
    MotorCycleEngineNumber: '',
    TransportationUse: null,
    CommercialVehicle: null,
    OwnerName: '',
    OwnerDL: '',
    OwnerDLState: '',
    CoOwnerName: '',
    CoOwnerDL: '',
    CoOwnerDLState: '',
    OwnerPhyAddr: '',
    OwnerMailAddr: '',
    CoOwnerPhyAddr: '',
    CoOwnerMailAddr: '',
    LegalOwnerName: '',
    LegalOwnerPhyAddr: '',
    LegalOwnerMailAddr: '',
    OdometerReading: null,
    VehiclePurchaseDate: '',
    VehiclePurchaseType: '',
    VehiclePurchaseLocation: '',
    VehicleEnteredStateDate: '',
    VehicleOperatedStateDate: '',
    VehicleCost: '',
    VehiclePurchasedFrom: '',
    BodyTypeMod: '',
    SalesTaxPaid: null,
    SalesTaxAmount: null,
    IsMemOfMilitary: null,
    IsMemOfMilitaryWhenLastLicensed: null,
    Scrapped: '',
    Status: '',
    V5cID: '',
    LeaseContractID: '',
    MSRP: null,
    SellingPrice: null,
    StateTax: null,
    State: ''
  };

  constructor(public http: Http) { }

  ngOnInit() {
  }

  noSubmit() {
    return false;
  }

  /* object methods */
  onSubmit() {
    console.log('vehicle' + this.vehicle.VIN);
    let url = 'http://kvrnsit.westus.cloudapp.azure.com:8080/blockchain/assets/vehicles';
    console.log('posting');
    return this.http.post(url,this.vehicle)
    .toPromise().then(
      function(res) {
        // display success to user
        console.log('post returned: ' + res);
      }
    ).catch(
      function(data){
        // display fail to user
        console.log('http post failed: ' + data);
      }
    );
  }
}



