import { Injectable } from '@angular/core';
import { Vehicle } from '../../interfaces/vehicle';
import { Http, Response,  Headers, RequestOptions } from '@angular/http';
import 'rxjs/add/operator/toPromise';


@Injectable()
export class VehicleLookupService {

  private vehicle: Vehicle = {
    v5cID:'',
    identity: 'DVLA',
    VIN: 'YV4902NK4F1209769',
    License: '',
    make: 'Mazda',
    model: '3',
    colour: 'Silver',
    Reg: '',
    year: 2000,
    FuelType: '',
    BodyType: '',
    StateLicenseNumber: '',
    VehicleType: '',
    MotorCycleEngineNumber: '',
    TransportationUse: null,
    CommercialVehicle: null,
    OwnerName: '',
    OwnerDL: '',
    OwnerDLState: '',
    CoOwnerName: '',
    CoOwnerDL: '',
    CoOwnerDLState: '',
    OwnerPhyAddr: '',
    OwnerMailAddr: '',
    CoOwnerPhyAddr: '',
    CoOwnerMailAddr: '',
    LegalOwnerName: '',
    LegalOwnerPhyAddr: '',
    LegalOwnerMailAddr: '',
    OdometerReading: null,
    VehiclePurchaseDate: '',
    VehiclePurchaseType: '',
    VehiclePurchaseLocation: '',
    VehicleEnteredStateDate: '',
    VehicleOperatedStateDate: '',
    VehicleCost: '',
    VehiclePurchasedFrom: '',
    BodyTypeMod: '',
    SalesTaxPaid: null,
    SalesTaxAmount: null,
    IsMemOfMilitary: null,
    IsMemOfMilitaryWhenLastLicensed: null,
    Scrapped: '',
    Status: '',
    V5cID: '',
    LeaseContractID: '',
    MSRP: null,
    SellingPrice: null,
    StateTax: null,
    State: 'CA'
  };
  constructor(private http:Http) { }

  getVehicles():Promise<Vehicle[]> {
    let body = {"identity": "DVLA"};
    let headers = new Headers({ 'Content-Type': 'application/json' });
    let options = new RequestOptions({ headers: headers});
    return this.http.post('http://kvrnsit.westus.cloudapp.azure.com:8080/blockchain/assets/getallvehicles',
                         body, options)
            .toPromise()
            .then(response => {
              return response.json();
              //return [this.vehicle, this.vehicle];
            })
            .catch((error) => {
              console.error('vehiclelookupservice.getVehicles', error.message || error);
              //return Promise.reject(error.message|| error);
              return [this.vehicle, this.vehicle];
            });
  }

}



